export default {
	data() {
		return {
			menu1Show : false,
			menu1Top  : 30
		};
	},
	methods: {
		showMenu1 : function(e){
			if(this.menu1Show){
				this.menu1Show = false;
				return ;
			}
			// 动态调整菜单的 top 值
			// 原理 : 获取按钮的高度 = 菜单的 top 值
			let view = uni.createSelectorQuery().select("#demo-btn-1");
			view.fields({size: true}, data => {
			  this.menu1Show = true;
			  this.menu1Top = data.height;
			}).exec();
		},
		// 关闭菜单方法
		hideMenu : function(){
			this.menu1Show = false;
		},
		// 菜单点击函数
		tap1 : function(){
			uni.showToast({
				title:"您点击了第1个菜单",
				icon: "none"
			});
			this.hideMenu();
		},
		tap2 : function(){
			uni.showToast({
				title:"您点击了第2个菜单",
				icon: "none"
			});
			this.hideMenu();
		},
		tap3 : function(){
			uni.showToast({
				title:"您点击了第3个菜单",
				icon: "none"
			});
			this.hideMenu();
		}
	},
	components:{
		gracePopupMenu
	}
}
